import { useDispatch, useSelector } from "react-redux";
import { changeFile } from "../../../redux/formReducer";
import CustomLabel from "../../UI/CustomLabel/CustomLabel";
import style from "./File.module.css";

const File = () => {
  const dispatch = useDispatch();
  const file = useSelector((state) => state.form.file);

  function handleChange(e) {
    if(!e.target.files.length) return
    dispatch(changeFile(e.target.files[0].name));
  }

  function removeFile(e) {
    if(e.target.classList.contains("remove")) e.target.value = ""
    dispatch(changeFile("Загрузите резюме"));
  }

  // className={"form-label " + style.fileLabel}
  return (
    <>
      <div className={style.file} onClick={removeFile}>
        <CustomLabel
          className={
            file.file == "Загрузите резюме"
              ? style.fileLabel + " form-label "
              : style.fileLabelActive + " form-label"
          }
          htmlFor="file"   
        >
          <span className={style.fileName}>{file.file}</span>
          {file.file != "Загрузите резюме" && (
            <span className={style.removeFile + " remove"}>
              x
            </span>
          )}
        </CustomLabel>
        <input
          type="file"
          id="file"
          placeholder="Выберите файл"
          className="form-control"
          onChange={handleChange}
        />
      </div>
    </>
  );
};

export default File;
